import React from 'react';
import fetch from 'isomorphic-unfetch';

import {apiKey} from '../apikey'

class Weather extends React.Component{

    static defaultProps = {
        name: "Enrique"
    }

    constructor(props){
        super(props);

        this.state = {
            zipCode: `20002`,
            countryCode: 'us',
            isLoaded: false,
            data: {
                name: "",
                initial: "",
            },
        }       
     
       
    }

    
    componentDidMount(){
        //useful for Async functions 
        this.getWeather().then( ( data ) => {
            this.setState({
                ...this.state,
                isLoaded: true,
                data: data
            })
        });
        //runs after component renders
        console.log("component did mount")
    }

    componentDidUpdate(prevProps, prevState, snapshot){
        //not in use
        console.log("component did update")
    }
    
  

    async getWeather(){
        let APIkey = apiKey.weatherApiKey;
        let URL = `http://api.openweathermap.org/data/2.5/weather?zip=${this.state.zipCode},${this.state.countryCode}&appid=${APIkey}units=imperial`;
        try {
            const res = await fetch(URL);
            if (res.ok) {
                const data = await res.json();
                console.log(data)
                return data
            }
            throw new Error('Error Message');    
        }
        catch(err){
            console.log(err)
        }
    }

    renderWidget(){
        return(
            <div>
                <h1>City: {this.state.data.name}</h1>
                <p>Current Temperature: {this.state.data.main.temp}</p>
                <p>Weather: {this.state.data.weather[0].main}</p>
                <p>Humidity: {this.state.data.main.humidity}</p>
                <p>Wind Speed: {this.state.data.wind.speed}</p>
            </div>
        )
    }
    

    render(){
        console.log("rendered")
        //render method should return JSX
        return(
            <div className = 'Weather'>
                
                <div>
                    <h1> Weather </h1>
                    <p> - List types of consults</p>
                    <p> - display retrieved information graphically </p>
                    <div> { this.state.isLoaded ? this.renderWidget() : 'Loading...' } </div>

                    <br></br> 

                    <p></p>
                    
                        <input id='zipCode-targeted'
                            
                            type = 'text'
                            name='zipCode'
                            value={this.state.zipCode}
                            ></input>
                            <br></br>
                        <button className='zipCode-btn'>Submit</button>
                
                    

                    <br></br> 

                    


                </div>

                <style jsx>
                    {`
                        *{
                            margin: 0;
                            padding: 0;
                            box-sizing: border-box;
                        }

                        .Weather{
                            background-color: rgba(160, 180, 230, .8)
                        }
                    `}
                </style>
            </div>
        )
    }
}

export default Weather;